.tp-hero {
	display: flex;
	justify-content: center;
	width: 100%;
	height: calc(100vh - #{$header-height});

	@include mq() {
		// height: calc (100vh - 66px);
	}

	&__img-wrapper {
		align-self: center;
	}

	&__logo {
		width: 309px;
		height: 628px;
		@include mq() {
			height: 240px;
			width: auto;
		}		
	}

}

.tp-section-header {
	position: relative;
	margin-bottom: 1em;
	white-space: nowrap;
	@include mq() {
		text-align: center;
		margin-bottom: 2em;
	}

	&::before {
		background: linear-gradient(rgb(83, 167, 116) 0%, rgb(20, 125, 60) 100%);
		width: 6px;
		height: 4em;
		content: "";
		display: inline-block;
		float: left;
		margin-right: 10px;
		
		@include mq() {
			display: none;
		}
	}

	&__title {
    font-size: 4rem;
    letter-spacing: 0.120em;
    line-height: 1.1;
    margin-bottom: 0;
	}

	&__title-ja {
		font-size: 1.2rem;
		letter-spacing: 0.120em;
		color: $color-secondary-text;
	}
}

.tp-section {
	padding: 4em 0;

	&--gray {
		background-color: $color-background-lightest;
	}

	&__text {
		line-height: 2;
		letter-spacing: 0.120em;
		font-size: 1.6rem;
		&-dot{
			color: $color-primary;
		}
		&--pc{
			margin-bottom: 5rem;
		}
	}

	&--service, &--vision {
		@include mq() {
			padding-bottom: 0;
		}
	}

	.c-btn {
		display: block;
		margin: 0 auto;
	}
}

.tp-header {
	font-size: 2.4rem;
	letter-spacing: 0.120em;
	margin-bottom: 1em;
	line-height: 1.6;
	@include mq() {
		font-size: 2rem;
		line-height: 1.8;
	}
}

.missionIcons{
	margin: 0 0 2rem 0;
	&__item{
		float: left;
		width: 23%;
		margin: 0 2.6% 0 0;
		@include mq-min(){
			width: 20%;
			margin: 0 6.6% 0 0;
		}
		&:last-child{
			margin: 0;
		}
		dt{
			margin: 0 0 1rem 0;
			img{
				max-width: 100%;
			}
		}
		dd{
			text-align: center;
			font-size: 1.2rem;
		}
	}
}//.missionIcons

.kaiketsu{
	text-align: center;
	font-weight: bold;
	font-size: 2rem;
	color: $color-primary;
	letter-spacing: 3px;
}

.serviceIcon{
	margin: 0 0 2rem 0;
	&__item{
		float: left;
		width: 17%;
		margin-right: 3.75%;
		&:first-child{
			margin-left: 0;
		}
		&:last-child{
			margin-right: 0;
		}
		dt{
			margin: 0 auto 1rem auto;
			text-align: center;
			img{
				width: 50%;
			}
		}
		dd{
			text-align: center;
			line-height: 1.3;
		}
	}
	&__en{
		font-size: 1.5rem;
		letter-spacing: 2px;
		@include mq-min(){
			font-size: 2.1rem;
			letter-spacing: 3px;
		}
		span{
			color: $color-primary;
		}
	}
	&__ja{
		font-size: 1.1rem;
		@include mq-min(){
			font-size: 1.4rem;
		}
	}
}//.missionIcons

.clientIcon{
	&__item{
		float: left;
		width: 28%;
		margin-right: 8%;
		margin-bottom: 4rem;
		text-align: center;
		&:nth-child(3n){
			margin-right: 0;
		}
		dt{
			margin: 0 0 2rem 0;
			img{
				max-width: 100%;
			}
		}
		dd{
			text-align: left;
			height: 2rem;
			line-height: 1.3;
			font-size: 2.2vw;
			@include mq-min(){
				font-size: 1.8rem;
			}
		}
	}
}//clientIcon


.blogHead{
	margin-bottom: 2rem;
	@include mq-min(){
		margin-top: 1rem;
		padding-top: 1rem;
		margin-bottom: 0;
		border-top: 2px dotted #aaa;
		display: flex;
		align-items: center;
		&:first-child{
			margin-top: 0;
			padding-top: 0;
			border-top: none;
		}
	}

	&__ph{
		width: 20%;
		float: left;
		margin-bottom: 0;
		@include mq-min(){
			width: 95px;
		}
		img{
			width: 100%;
			@include mq-min(){
				width: 95px;
			}
		}
	}//ph

	&__detail{
		padding-left: 23%;
		@include mq-min(){
			padding-left: 2rem;
		}
		&__title{
			line-height: 1.5;
			letter-spacing: 2px;
			@include mq-min(){
				font-size: 2.4rem;
			}
		}
		&__meta{
			padding: 0;
		}
		&__date{
			color: #646464;
			font-size: 1.6rem;
			float: left;
			margin: 0 1rem 0 0;
			letter-spacing: 2px;
		}
		&__cat{
			float: left;
			margin: 0 1rem 0 0;
			a{
				padding: 0.5rem 1rem;
				color: #fff;
				background-color: #646464;
				border-radius: 5px;
				font-size: 1.2rem;
				line-height: 1.2;
				&:hover{
					background-color: $color-primary;
					text-decoration: none;
				}
			}
		}
		li{
			list-style: none;
		}
	}//detail
}//blogHead


