		&:first-child{
			border-top: none;
			margin-top: 0;
			padding-top: 0;
		}
	}
	&__ph{
		display: none;
		@include mq-min(){
			display: block;
			width: 69px;
			float: left;
			margin-bottom: 0;
			img{
				width: 100%;
			}
		}
