		img{
			max-width: 100%;
		}
		@include mq-min(){
			width: 100%;
			margin: 0 0 3rem 0;
			img{
				max-width: 100%;
			}
		}
	}
	&__title{
		text-align: center;
		margin: 0 0 2rem 0;
