.tablenav {
	text-align: center;
	margin: 2rem 0 5rem 0;
	.current {
		background: #147d3c; /* Old browsers */
		background: -moz-linear-gradient(top, #147d3c 0%, #53a774 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(top, #147d3c 0%,#53a774 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to bottom, #147d3c 0%,#53a774 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		color: white;
	}
	.next, .prev {
		font-size: 1.6rem;
		height: 70px;
		line-height: 1;
	}
	a.page-numbers {
		font-size: 1.6rem;
		padding: 1rem 1.2rem 0.5rem 1.2rem;
		border-right: 1px solid #ccc;
		border-top: 1px solid #ccc;
		border-bottom: 1px solid #ccc;
		&:first-child{
			-webkit-border-radius: 5px 0 0 5px;
			border-radius: 5px 0 0 5px;
			border: 1px solid #ccc;
		}
		&:last-child{
			-webkit-border-radius: 0 5px 5px 0;
			border-radius: 0 5px 5px 0;
			border-right: 1px solid #ccc;
			border-top: 1px solid #ccc;
			border-bottom: 1px solid #ccc;
		}
	}
	a.page-numbers:hover {
		background: #147d3c; /* Old browsers */
		background: -moz-linear-gradient(top, #147d3c 0%, #53a774 100%); /* FF3.6-15 */
		background: -webkit-linear-gradient(top, #147d3c 0%,#53a774 100%); /* Chrome10-25,Safari5.1-6 */
		background: linear-gradient(to bottom, #147d3c 0%,#53a774 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
		color: white;
		text-decoration: none;
	}
}//tableNav



.blogHeader{
	margin: 0 0 4rem 0;
	@include mq-min(){
		padding: 0 0 2rem 0;
		border-bottom: 2px dotted #aaa;
	}
	&__title{
		line-height: 1.5;
		@include mq-min(){
			font-size: 3.2rem;
		}
	}
	&__meta{
		padding: 0;
		color: #646464;
		font-size: 1.6rem;
		&__date{
			float: left;
			list-style: none;
			margin-right: 1rem;
		}
		&__cat{
			float: left;
			list-style: none;
			margin-right: 1rem;
