.postBody{
	font-size: 1.8rem;
	line-height: 2;
	p{
		margin-bottom: 1.5rem;
	}
	img{
		width: 100%;
		margin: 0 0 3rem 0;
	}
	strong{
		font-weight: bold;
	}
	em{
		font-style: italic;
		color: #777;
	}
	h2,h3{
		font-weight: bold;
		line-height: 1.5;
		margin: 7rem 0 1.5rem 0;
		font-size: 2.4rem;
	}
	blockquote{
		background-color: #f9f9f9;
		padding: 2rem;
